import { useEffect, useState, useRef } from "react";
import {
  Typography,
  Box,
  Paper,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from "@mui/material";
import axios from "axios";
import socket from "../socket";

export default function ASectionServerPage() {
  const [orders, setOrders] = useState([]);
  const [confirmData, setConfirmData] = useState(null);

  const fetchInitialOrders = async () => {
    try {
      const res = await axios.get("https://festival-backend-wwht.onrender.com/api/kitchen/A");
      setOrders(res.data);
    } catch (err) {
      console.error("    :", err);
    }
  };

  const confirmServe = async () => {
    if (!confirmData) return;
    const { timestamp, itemIndex } = confirmData;

    try {
      await axios.patch(
        `https://festival-backend-wwht.onrender.com/api/kitchen/${timestamp}/${itemIndex}/serve`
      );

      setOrders((prev) =>
        prev.filter(
          (item) =>
            !(item.timestamp === timestamp && Number(item.itemIndex) === Number(itemIndex))
        )
      );

      socket.emit("orderDeleted", {
        timestamp,
        itemIndexes: [itemIndex],
      });
      socket.emit("orderServed", { zone: "A", timestamp, itemIndex }); // emit

      setConfirmData(null);
    } catch (err) {
      console.error("   :", err);
      alert("  .");
    }
  };

  useEffect(() => {
    fetchInitialOrders();

    const handleNewOrder = (data) => {
      if (Array.isArray(data)) {
        console.log(" A   :", data);
        setOrders((prev) => [...prev, ...data]);
      }
    };

     //   
    const handleOrderDeleted = ({ timestamp, itemIndexes }) => {
      console.log(" A  :", timestamp, itemIndexes);
      setOrders((prev) =>
        prev.filter(
          (order) =>
            !(
              order.timestamp === timestamp &&
              itemIndexes.includes(order.itemIndex)
            )
        )
      );
    };


    const handleOrderServed = ({ timestamp, itemIndexes }) => {
      console.log(" A   :", timestamp, itemIndexes);
      setOrders((prev) =>
        prev.filter(
          (order) =>
            !(
              order.timestamp === timestamp &&
              itemIndexes.includes(order.itemIndex)
            )
        )
      );
    };

    socket.on("order:A", handleNewOrder);
    socket.on("orderServed", handleOrderServed);
    socket.on("orderDeleted", handleOrderDeleted);

    return () => {
      socket.off("order:A", handleNewOrder);
      socket.off("orderServed", handleOrderServed);
      socket.off("orderDeleted", handleOrderDeleted);
    };
  }, []);

  return (
    <Box p={3}>
      <Typography variant="h5" gutterBottom>
         A   
      </Typography>

      {orders.length === 0 ? (
        <Typography color="textSecondary">  .</Typography>
      ) : (
        orders.map((item) => (
          <Paper
            key={`${item.timestamp}-${item.itemIndex}`}
            elevation={2}
            sx={{ p: 2, mb: 2, borderLeft: "4px solid #4caf50" }}
          >
            <Typography variant="h3" fontWeight={600}>
              {item.name} ({item.quantity})
            </Typography>
            <Typography variant="h5" color="error" sx={{ mb: 1 }}>
               : {item.tableNumber}
            </Typography>
            <Button
              variant="contained"
              color="success"
              size="small"
              onClick={() =>
                setConfirmData({
                  timestamp: item.timestamp,
                  itemIndex: item.itemIndex,
                  name: item.name,
                })
              }
            >
                 
            </Button>
          </Paper>
        ))
      )}

      <Dialog open={!!confirmData} onClose={() => setConfirmData(null)}>
        <DialogTitle>  </DialogTitle>
        <DialogContent>
           <strong>{confirmData?.name}</strong>   ?
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setConfirmData(null)}></Button>
          <Button onClick={confirmServe} variant="contained" color="primary">
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}