import { useEffect, useState } from "react";
import io from "socket.io-client";
import {
  Box,
  Typography,
  Card,
  CardContent,
  List,
  ListItem,
  ListItemText,
  Divider,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
} from "@mui/material";
import axios from "axios";

const socket = io("https://festival-backend-wwht.onrender.com");

export default function DrunkPage() {
  const [orders, setOrders] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [selectedOrderId, setSelectedOrderId] = useState(null);

  const menuDetail = {
    '': 2,
    '': 2,
    '': 2,
    ' ': 2,
    '': 1,
    '': 1,
    '': 1,
    '': 1,
    '':1
  };

  //      ( )
  const calculateDrinking = (items) => {
    let total = 0;
    items.forEach((item) => {
      console.log(" :", item.name, ":", item.quantity);
      if (menuDetail[item.name] && item.quantity > 0) {
        total += menuDetail[item.name] * item.quantity;
      }
    });
    return total;
  };

  //    
  async function fetchDrunkOrders() {
    try {
      const res = await axios.get("https://festival-backend-wwht.onrender.com/api/drunk-orders");

      const enrichedOrders = res.data
          .filter((order) => order.drinkingDelivered === false) //     
          .map((order) => ({
              ...order,
              drinking: calculateDrinking(order.items),
      }));


      setOrders(enrichedOrders);
    } catch (err) {
      console.error("   :", err);
    }
  }
  useEffect(() => {

    fetchDrunkOrders();
    console.log(orders);
  }, []);

  //    
  useEffect(() => {
    socket.on("order:drunk", (data) => {
        fetchDrunkOrders();
    });

    return () => {
        socket.off("order:drunk");
    };
}, []);

//   
const handleClickDelivered = (id) => {
    setSelectedOrderId(id);
    setModalOpen(true);
};

  //    
  const confirmDrinkingDelivered = async () => {
    if (!selectedOrderId) {
      console.warn("  ID .");
      return;
    }

    try {
      await axios.patch(
        `https://festival-backend-wwht.onrender.com/api/drunk-orders/${selectedOrderId}/drinking-delivered`,
        { delivered: true }
      );

      setOrders((prev) => prev.filter((order) => order.id !== selectedOrderId));
    } catch (err) {
      console.error("  :", err);
    } finally {
      setModalOpen(false);
      setSelectedOrderId(null);
    }
  };

  return (
    <Box sx={{ padding: 4 }}>
      <Typography variant="h4" gutterBottom>
            
      </Typography>

      {orders.length === 0 ? (
        <Typography color="text.secondary">  .</Typography>
      ) : (
        orders.map((order, index) => (
          <Card key={index} variant="outlined" sx={{ mb: 3 }}>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                 {order.tableNumber} ( : {order.timestamp})
              </Typography>
              <Typography variant="body2" sx={{ mb: 1 }}>
                  : {order.drinking || 0}
              </Typography>
              <List dense>
                {order.items.map((item, idx) => (
                  <div key={idx}>
                    <ListItem>
                      <ListItemText
                        primary={`${item.name} x ${item.quantity}`}
                        secondary={`: ${item.zone}`}
                      />
                    </ListItem>
                    {idx !== order.items.length - 1 && <Divider />}
                  </div>
                ))}
              </List>
              <Button
                variant="outlined"
                color="warning"
                onClick={() => handleClickDelivered(order.id)}
                sx={{ mt: 2 }}
              >
                    (  )
              </Button>
            </CardContent>
          </Card>
        ))
      )}

      {/*   */}
      <Dialog open={modalOpen} onClose={() => setModalOpen(false)}>
        <DialogTitle>  </DialogTitle>
        <DialogContent>
          <Typography>    ?</Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setModalOpen(false)}></Button>
          <Button
            onClick={confirmDrinkingDelivered}
            color="success"
            variant="contained"
            disabled={!selectedOrderId} //   
          >
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}
