import React, { useEffect, useState } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Typography,
  Box,
  List,
  ListItem,
  ListItemText,
  IconButton,
  Divider,
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import axios from "axios";
import MenuSelectModal from "./MenuSelectModal";
import TableInputModal from "./TableInputModal";
import saveToLocal from '../function/saveToLocal';

export default function OrderCreateModal({ open, onClose, onOrderComplete }) {
  const [menuList, setMenuList] = useState([]);
  const [cart, setCart] = useState([]);
  const [selectedMenu, setSelectedMenu] = useState(null);
  const [isTableModalOpen, setIsTableModalOpen] = useState(false);
  const [isSummaryOpen, setIsSummaryOpen] = useState(false);
  const [tableNumber, setTableNumber] = useState(null);
  const [outsider,setOutsider] = useState(false);
  const menuDetail = {
    '':2,
    '':2,
    '':2,
    ' ':2,
    '':1,
    '':1,
    '':1,
    '':1,
    '':0
  }
  

  const fetchMenu = async () => {
    try {
      const res = await axios.get("https://festival-backend-qydq.onrender.com/api/menu");
      setMenuList(res.data);
    } catch (err) {
      console.error("  :", err);
    }
  };

  useEffect(() => {
    if (open) {
      fetchMenu();
      setCart([]);
      setTableNumber(null);
    }
  }, [open]);

  const handleAddToCart = (item, quantity) => {
    let isExist = false;
    const updatedCart = cart.map((data) => {
      if (data.name === item.name) {
        isExist = true;
        return {
          ...data,
          quantity: data.quantity + quantity,
          total: data.total + quantity * item.price,
        };
      }
      return data;
    });

    if (!isExist) {
      const total = item.price * quantity;
      const cartItem = {
        ...item,
        quantity,
        total,
        served: false,
      };
      setCart([...cart, cartItem]);
    } else {
      setCart(updatedCart);
    }

    setSelectedMenu(null);
  };

  const handleRemoveItem = (index) => {
    const updated = [...cart];
    updated.splice(index, 1);
    setCart(updated);
  };

  const handleOutsider = () => {
    setOutsider(!outsider);
  }

  const handleOrderSubmit = () => {
    const updatedCart = outsider
      ? cart.map((item) => {
          if (menuDetail[item.name]) {
            const surcharge = menuDetail[item.name] === 2 ? 4000 : menuDetail[item.name] === 1 ? 2000 : 0;
            return {
              ...item,
              price: item.price + surcharge,
              total: item.total + surcharge,
            };
          }
          return item;
        })
      : cart;

    const totalPrice = updatedCart.reduce((acc, item) => acc + item.total, 0);

    const orderData = {
      tableNumber,
      items: updatedCart,
      totalPrice,
      timestamp: new Date().toISOString(),
      served: false,
      outsider: outsider,
    };

    //    
    axios
      .post("https://festival-backend-qydq.onrender.com/api/orders/complete", orderData)
      .then(() => {
        onOrderComplete();
        setIsSummaryOpen(false);
        saveToLocal();
        onClose();
      })
      .catch((err) => {
        console.error("  :", err);
        alert("  .");
      });

    //      
    if (outsider) {
      let drinking = 0;
      updatedCart.forEach((item) => {
        if (menuDetail[item.name]) {
          drinking += menuDetail[item.name] * item.quantity;
        }
      });

      const drunkOrderData = {
        tableNumber,
        items: updatedCart.map(({ name, quantity, zone }) => ({ name, quantity, zone })),
        timestamp: new Date().toISOString(),
        drinking,
      };

      axios
        .post("https://festival-backend-qydq.onrender.com/api/drunk-orders", drunkOrderData)
        .then((res) => {
          console.log("     :", res.data);
        })
        .catch((err) => {
          console.error("     :", err);
        });
    }

};



  return (
    <>
     <Dialog open={open} onClose={onClose} fullWidth maxWidth="md">
      <DialogTitle>  </DialogTitle>
      <DialogContent sx={{ display: "flex", gap: 2 }}>
        {/*  -   */}
        <Box sx={{ flex: 1, backgroundColor: "#f5f5f5", p: 2 }}>
          <Typography variant="h6" gutterBottom>
             
          </Typography>
          <List>
            {cart.map((item, idx) => (
              <ListItem key={idx} divider>
                <ListItemText
                  primary={`${item.name} (${item.quantity})`}
                  secondary={`${item.total.toLocaleString()}`}
                />
                <IconButton onClick={() => handleRemoveItem(idx)}>
                  <DeleteIcon />
                </IconButton>
              </ListItem>
            ))}
          </List>
          <Box sx={{ textAlign: "right", mt: 2 }}>
            <Typography variant="subtitle1">
              : {cart.reduce((acc, item) => acc + item.total, 0).toLocaleString()}
            </Typography>
            <Button
              variant="contained"
              color="primary"
              disabled={cart.length === 0}
              onClick={() => setIsTableModalOpen(true)}
              sx={{ mt: 1 }}
            >
               
            </Button>
          </Box>
        </Box>

        {/*  -   */}
        <Box
          sx={{
            flex: 1,
            backgroundColor: "#fff",
            p: 2,
            maxHeight: "60vh",
            overflowY: "auto",
          }}
        >
          <Typography variant="h6" gutterBottom>
             
          </Typography>
          <List>
            {menuList.map((item, idx) => (
              <React.Fragment key={idx}>
                <ListItem button onClick={() => setSelectedMenu(item)}>
                  <ListItemText
                    primary={`${item.name} (${item.zone})`}
                    secondary={`${item.price.toLocaleString()}`}
                  />
                </ListItem>
                <Divider />
              </React.Fragment>
            ))}
          </List>
        </Box>
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}></Button>
      </DialogActions>
    </Dialog>


      {selectedMenu && (
        <MenuSelectModal
          menu={selectedMenu}
          onClose={() => setSelectedMenu(null)}
          onConfirm={handleAddToCart}
        />
      )}

      {isTableModalOpen && (
        <TableInputModal
          open={isTableModalOpen}
          onClose={() => setIsTableModalOpen(false)}
          onSubmit={(num) => {
            if (num < 1 || num > 100) {
              alert("  1~100  .");
              return;
            }
            setTableNumber(num);
            setIsTableModalOpen(false);
            setIsSummaryOpen(true);
          }}
        />
      )}

      <Dialog open={isSummaryOpen} onClose={() => setIsSummaryOpen(false)}>
  <DialogTitle>  </DialogTitle>
  <DialogContent>
    <Typography variant="subtitle1"> : {tableNumber}</Typography>
    <List>
      {cart.map((item, idx) => {
        let adjustedPrice = item.price;
        let adjustedTotal = item.total;

        if (outsider && menuDetail[item.name]) {
          const surcharge = menuDetail[item.name] === 2 ? 4000 : menuDetail[item.name] === 1 ? 2000 : 0;
          adjustedPrice = item.price + surcharge;
          adjustedTotal = adjustedPrice * item.quantity;
        }

        return (
          <ListItem key={idx}>
            <ListItemText
              primary={`${item.name} (${item.quantity})`}
              secondary={`: ${adjustedPrice.toLocaleString()} / : ${adjustedTotal.toLocaleString()}`}
            />
          </ListItem>
        );
      })}
    </List>
    <Typography sx={{ mt: 2 }}>
      :{" "}
      {cart
        .reduce((acc, item) => {
          if (outsider && menuDetail[item.name]) {
            const surcharge = menuDetail[item.name] === 2 ? 4000 : menuDetail[item.name] === 1 ? 2000 : 0;
            return acc + (item.price + surcharge) * item.quantity;
          } else {
            return acc + item.total;
          }
        }, 0)
        .toLocaleString()}
      
    </Typography>
  </DialogContent>
  <DialogActions>
    <Button
      variant="contained"
      sx={{
        backgroundColor: outsider ? 'red' : 'primary.main',
        '&:hover': {
          backgroundColor: outsider ? 'darkred' : 'primary.dark',
        },
      }}
      onClick={handleOutsider}
    >
      {outsider ? '' : ''}
    </Button>
    <Button onClick={handleOrderSubmit} variant="contained" color="primary">
       
    </Button>
    <Button onClick={() => setIsSummaryOpen(false)}></Button>
  </DialogActions>
</Dialog>

    </>
  );
}
