import React, { useEffect, useState } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  List,
  ListItem,
  ListItemText,
  Typography,
  Divider,
} from "@mui/material";
import axios from "axios";
import saveToLocal from '../function/saveToLocal';

export default function MenuDeleteModal({ open, onClose, onUpdate }) {
  const [menuList, setMenuList] = useState([]);
  const [selectedMenu, setSelectedMenu] = useState(null);

  //  
  const fetchMenu = async () => {
    try {
      const res = await axios.get("https://festival-backend-qydq.onrender.com/api/menu");
      setMenuList(res.data);
    } catch (err) {
      console.error("  :", err);
    }
  };

  useEffect(() => {
    if (open) {
      fetchMenu();
      setSelectedMenu(null); // 
    }
  }, [open]);

  //  
  const handleDelete = async () => {
    if (!selectedMenu) return;
    try {
      await axios.delete(`https://festival-backend-qydq.onrender.com/api/menu/${selectedMenu.index}`);

      saveToLocal();
      onUpdate(); //  
      onClose();
    } catch (err) {
      console.error(" :", err);
      alert(" .");
    }
  };

  return (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth={selectedMenu ? "xs" : "sm"}>
      <DialogTitle>{selectedMenu ? " " : "  "}</DialogTitle>
      <DialogContent>
        {selectedMenu ? (
          <Typography>
            <strong>{selectedMenu.name}</strong>  ?
          </Typography>
        ) : (
          <List>
            {menuList.map((item, idx) => (
              <React.Fragment key={idx}>
                <ListItem button onClick={() => setSelectedMenu(item)}>
                  <ListItemText
                    primary={`${item.name} (${item.zone})`}
                    secondary={`${item.price.toLocaleString()}`}
                  />
                </ListItem>
                <Divider />
              </React.Fragment>
            ))}
          </List>
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}></Button>
        {selectedMenu && (
          <Button color="error" variant="contained" onClick={handleDelete}>
            
          </Button>
        )}
      </DialogActions>
    </Dialog>
  );
}
