import React, { useState } from "react";
import { Dialog, DialogTitle, DialogContent, DialogActions, Button, TextField, MenuItem } from "@mui/material";
import axios from "axios";
import saveToLocal from '../function/saveToLocal';

const zones = ["A", "B", "C", "Counter"];

export default function AddMenuModal({ open, onClose, onAdd }) {
  const [name, setName] = useState("");
  const [price, setPrice] = useState("");
  const [zone, setZone] = useState("A");

  const handleSubmit = async () => {
    if (!name || !price || !zone) return alert("  ");

    const newItem = {
      name,
      price: parseInt(price),
      zone,
    };

    try {
      await axios.post("https://festival-backend-qydq.onrender.com/api/menu", newItem);

      saveToLocal();
      onAdd();
      onClose();
    } catch (err) {
      console.error("  :", err);
      alert("  .");
    }
  };

  const handleClose = () => {
    setName("");
    setPrice("");
    setZone("A");
    onClose();
  };

  return (
    <Dialog open={open} onClose={handleClose} fullWidth maxWidth="xs">
      <DialogTitle>  </DialogTitle>
      <DialogContent>
        <TextField
          autoFocus
          margin="dense"
          label=" "
          fullWidth
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
        <TextField
          margin="dense"
          label=""
          type="number"
          fullWidth
          value={price}
          onChange={(e) => setPrice(e.target.value)}
        />
        <TextField
          margin="dense"
          select
          label=" "
          fullWidth
          value={zone}
          onChange={(e) => setZone(e.target.value)}
        >
          {zones.map((z) => (
            <MenuItem key={z} value={z}>
              {z} 
            </MenuItem>
          ))}
        </TextField>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose}></Button>
        <Button variant="contained" onClick={handleSubmit}></Button>
      </DialogActions>
    </Dialog>
  );
}
