import React, { useState, useEffect } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  TextField,
  MenuItem,
  List,
  ListItem,
  ListItemText,
  Divider
} from "@mui/material";
import axios from "axios";
import saveToLocal from '../function/saveToLocal';

const zones = ["A", "B", "C", "Counter"];

export default function EditMenuModal({ open, onClose, onUpdate }) {
  const [menuList, setMenuList] = useState([]);
  const [selectedMenu, setSelectedMenu] = useState(null);
  const [edited, setEdited] = useState({ name: "", price: "", zone: "A" });

  const fetchMenu = async () => {
    try {
      const res = await axios.get("https://festival-backend-qydq.onrender.com/api/menu");
      setMenuList(res.data);
    } catch (err) {
      console.error("  :", err);
    }
  };

  useEffect(() => {
    if (open) {
      fetchMenu();
      setSelectedMenu(null); // 
    }
  }, [open]);

  const handleSelect = (menu) => {
    setSelectedMenu(menu);
    setEdited({ name: menu.name, price: menu.price, zone: menu.zone });
  };

  const handleSave = async () => {
    if (!selectedMenu) return;
    try {
      await axios.put(`https://festival-backend-qydq.onrender.com/api/menu/${selectedMenu.index}`, {
        name: edited.name,
        price: parseInt(edited.price),
        zone: edited.zone,
      });

      saveToLocal();
      onUpdate();
      onClose();
    } catch (err) {
      console.error(" :", err);
      alert(" .");
    }
  };

  return (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth={selectedMenu ? "xs" : "sm"}>
      <DialogTitle>{selectedMenu ? " " : "  "}</DialogTitle>
      <DialogContent>
        {selectedMenu ? (
          <>
            <TextField
              margin="dense"
              label=" "
              fullWidth
              value={edited.name}
              onChange={(e) => setEdited({ ...edited, name: e.target.value })}
            />
            <TextField
              margin="dense"
              label=""
              type="number"
              fullWidth
              value={edited.price}
              onChange={(e) => setEdited({ ...edited, price: e.target.value })}
            />
            <TextField
              margin="dense"
              select
              label=" "
              fullWidth
              value={edited.zone}
              onChange={(e) => setEdited({ ...edited, zone: e.target.value })}
            >
              {zones.map((z) => (
                <MenuItem key={z} value={z}>{z} </MenuItem>
              ))}
            </TextField>
          </>
        ) : (
          <List>
            {menuList.map((menu, idx) => (
              <React.Fragment key={idx}>
                <ListItem button onClick={() => handleSelect(menu)}>
                  <ListItemText
                    primary={`${menu.name} (${menu.zone})`}
                    secondary={`${menu.price.toLocaleString()}`}
                  />
                </ListItem>
                <Divider />
              </React.Fragment>
            ))}
          </List>
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}></Button>
        {selectedMenu && (
          <Button variant="contained" onClick={handleSave}>
            
          </Button>
        )}
      </DialogActions>
    </Dialog>
  );
}
